import * as d3 from 'd3';
import React, {Component} from 'react';
import StatisticsUtilities from "../utilities/statistics_utilities";
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';


export default class LatencyStatsComponent extends Component {
  constructor(props) {
    super(props);
    this.data = this.props.data;
  }

  update() {
    let sorted = [...this.data.map(d => d.value)].sort(d3.ascending);
    let [q1, q3, median, min, max, std] = StatisticsUtilities
      .calculate_important_values(sorted)
      .map(d => d.toFixed(1));
    this.setState({
      min: min,
      max: max,
      q1: q1,
      q3: q3,
      median: median,
      jitter: std
    });
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (!prevProps.data) {
      console.warn('data empty', prevProps.data);
    }
    if (this.data === prevProps.data) {
      return;
    }
    this.data = prevProps.data || [];
    this.update();
  }

  render() {
    return (
      <>
        {this.state &&
          (<Row style={{fontSize: 13, fontWeight: 'bold', whiteSpace: 'nowrap'}}>
            <Col sm={6}>
              min: {this.state.min}ms<br/>
              median: {this.state.median}ms<br/>
              max: {this.state.max}ms
            </Col>
            <Col sm={6}>
              25%: {this.state.min}ms<br/>
              75%: {this.state.median}ms<br/>
              jitter: {this.state.jitter}ms
            </Col>
          </Row>)
        }
      </>
    )
  }
}