import React, {Component} from 'react';
import Navbar from "react-bootstrap/Navbar";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Button from "react-bootstrap/Button";
import LatencyBoxChart from "../components/latency_box_chart";


export default class BoxPlotSample extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [1, 2, 3, 4].map((d) => {return {value: d, time: (Date.now() + d)}})
      //data: []
    }
  }

  add_data() {
    this.i = this.i || 1;
    this.i++;
    this.state.data.push({value:this.i});
    this.setState({
      data: this.state.data
    });
  }

  render() {
    return (
      <>
        <Navbar bg="light">
          <Navbar.Brand href="#">KNP</Navbar.Brand>
        </Navbar>
        <Container>
          <Row>
            <Button onClick={this.add_data.bind(this)}>Add Data!</Button>
          </Row>
          <Row>
            <LatencyBoxChart
              tooltip_html={"latency: ${d.value}</br>timestamp: ${d.time}"}
              width={800}
              height={100}
              data={this.state.data}
            />
          </Row>
        </Container>
      </>
    );
  }
}
