export default class Downloader {
  constructor(measurement_callback) {
    this.measurement_callback = measurement_callback;
  }

  get_http_fetch(size, i) {
    return fetch(`https://speed${i+1}.arshankhanifar.workers.dev/hi/down?bytes=${size}`);
  }

  convert_to_mbps(r) {
    return r*8/1e3;
  }

  async measure(size, streams) {
    let start = Date.now();
    let requests = Array(streams)
      .fill(0)
      .map(_ => { return {} })
      .map((req, i) => {
        req.fetch = this.get_http_fetch(size/streams, i);
        return req;
      });
    await Promise.all(requests.map(req => req.fetch.then(res => res.text())));
    let finish = Date.now();
    let rate_bpms = ((size)/(finish - start));
    return this.convert_to_mbps(rate_bpms);
  }
}