import Stage from "./stage";
import LatencyCollector from "../tools/latency_collector";
import {AppConfiguration} from "../app_configuration";
import Downloader from "../tools/downloader";


export default class DownlinkMeasurementStage extends Stage {
  constructor(data_callback, bandwidth_callback, done_callback) {
    super();
    this.data_callback = data_callback;
    this.bandwidth_callback = bandwidth_callback;
    this.done_callback = done_callback;
  }

  async run() {
    super.run();
    let urls = [
      "https://fonts.gstatic.com/s/roboto/v18/KFOmCnqEu92Fr1Mu4mxK.woff2",
      "https://fonts.gstatic.com/l/font?kit=KFOmCnqEu92Fr1Me4GZNCzcPK4I&skey=a0a0114a1dcab3ac&v=v20",
      "https://speed.cloudflare.com/__down?bytes=0"
    ];
    let url = urls[1];
    const collector = new LatencyCollector(
      url,
      AppConfiguration.PING_INTERVAL,
      this.data_callback,
      null,
      1e5
    );
    collector.start();
    let download_speed = await new Downloader().measure(1e8, 8);
    collector.cancel();
    this.bandwidth_callback(download_speed);
    this.done_callback();
  }
}