import Timer from "../utilities/timer";


export default class LatencyCollector {
  constructor(url, interval, interval_cb, done_cb, count) {
    this.url = url;
    this.interval = interval;
    this.interval_cb = interval_cb;
    this.done_cb = done_cb;
    this.count = count;
    this.cancelled = false;
  }

  async start() {
    let i = 0;
    while (i++ < this.count) {
      await fetch(this.url).then(res => res.text());
      let perf  = performance.getEntriesByName(this.url).pop();
      let ttfb = perf.responseStart - perf.requestStart;
      if (this.cancelled) {
        return ;
      }
      this.interval_cb(ttfb);
      await Timer.sleep(this.interval)
    }
    if (this.done_cb) {
      this.done_cb();
    }
  }

  cancel() {
    this.cancelled = true;
  }
}
