import Stage from "./stage";
import LatencyCollector from "../tools/latency_collector";
import {AppConfiguration} from "../app_configuration";


export default class LatencyMeasurementStage extends Stage {
  constructor(data_callback, done_callback) {
    super();
    this.data_callback = data_callback;
    this.done_callback = done_callback;
  }

  async run() {
    super.run();
    let urls = [
      "https://fonts.gstatic.com/s/roboto/v18/KFOmCnqEu92Fr1Mu4mxK.woff2",
      "https://fonts.gstatic.com/l/font?kit=KFOmCnqEu92Fr1Me4GZNCzcPK4I&skey=a0a0114a1dcab3ac&v=v20",
      "https://speed.cloudflare.com/__down?bytes=0"
    ];
    let url = urls[1];
    let collector = new LatencyCollector(
      url,
      AppConfiguration.PING_INTERVAL,
      this.data_callback,
      this.done_callback,
      20
    );
    collector.start();
  }
}